<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card style="margin-top: 10px">
      <el-row :gutter="20" style="margin-bottom: 10px">
        <el-col :span="6">
          <el-input placeholder="" v-model="queryInfo" clearable @input="queryChange">
            <el-button slot="append" icon="el-icon-search" @click="queryStudentList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!---->
      <el-table :data="studentList" border stripe :default-sort = "{prop: 'sno', order: 'ascending'}">
        <el-table-column label="" prop="sno" sortable></el-table-column>
        <el-table-column label="" prop="sname"></el-table-column>
        <el-table-column label="" prop="ssex"
                         :filters="[{text: '', value: ''}, {text: '', value: ''}]"
                         :filter-method="filterSexHandler"></el-table-column>
        <el-table-column label="" prop="sbirthday" sortable></el-table-column>
        <el-table-column label="" prop="classno" sortable
                         :filters="classnoFilters"
                         :filter-method="filterClassnoHandler"></el-table-column>
        <el-table-column label="" width="120px">
          <template slot-scope="scope">
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" size="mini"
                         @click="showEditDialog(scope.row.sno)"></el-button>
            </el-tooltip>
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" size="mini"
                         @click="deleteStudentById(scope.row.sno)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

<!--      <el-pagination-->
<!--          @size-change="sizeChange"-->
<!--          @current-change="currentChange"-->
<!--          :current-page="currentPage"-->
<!--          :page-sizes="[5, 10, 20, 50]"-->
<!--          :page-size="pageSize"-->
<!--          layout="total, sizes, prev, pager, next, jumper"-->
<!--          :total="totalLength"-->
<!--      >-->
<!--      </el-pagination>-->
    </el-card>

    <!---->
    <el-dialog
        title=""
        :visible.sync="addDialogVisible"
        width="40%"
        @close="addDialogClosed">
      <!---->
      <el-form :model="addForm" :rules="addRules" ref="addFormRef" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="sno">
          <el-input v-model="addForm.sno" class="item"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sname">
          <el-input v-model="addForm.sname" class="item"></el-input>
        </el-form-item>
        <el-form-item label="" prop="ssex">
          <el-select v-model="addForm.ssex" placeholder="" class="item">
            <el-option
                v-for="item in sexOptions"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="sbirthday">
          <el-date-picker
              v-model="addForm.sbirthday"
              type="date"
              :clearable="false"
              :editable="false"
              value-format="yyyy-MM-dd"
              placeholder=""
              class="item">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="classno">
          <el-select v-model="addForm.classno" placeholder="" class="item">
            <el-option
                v-for="item in classOptions"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <!---->
      <span slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible= false"> </el-button>
                <el-button type="primary" @click="createStudent"> </el-button>
            </span>
    </el-dialog>

    <!---->
    <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="40%"
        @close="editDialogClosed">
      <!---->
      <el-form :model="editForm" :rules="editRules" ref="editFormRef" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="sno">
          <el-input v-model="editForm.sno" class="item" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="sname">
          <el-input v-model="editForm.sname" class="item"></el-input>
        </el-form-item>
        <el-form-item label="" prop="ssex">
          <el-select v-model="editForm.ssex" placeholder="" class="item">
            <el-option
                v-for="item in sexOptions"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="sbirthday">
          <el-date-picker
              v-model="editForm.sbirthday"
              type="date"
              :clearable="false"
              :editable="false"
              value-format="yyyy-MM-dd"
              placeholder=""
              class="item">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="classno">
          <el-select v-model="editForm.classno" placeholder="" class="item">
            <el-option
                v-for="item in classOptions"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <!---->
      <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisible= false"> </el-button>
                <el-button type="primary" @click="modifyStudent()"> </el-button>
            </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
  name: "Student",
  data() {
    // 
    const rulesName = (rule, value, callback) => {
      // Axios
      this.$http.get('student/checkSno/' + value)
          .then((res => {
            // 
            if (res.data.code === 0) {
              callback()
            } else {
              callback(new Error(""))
            }
          }))
          .catch((err) => {
            // 
            console.log(err)
          })
    }
    return {
      // 
      addRules: {
        sno: [{
          required: true,
          message: '',
          trigger: 'blur'
        },
          {
            validator: rulesName,
            trigger: 'blur'
          },
          {
            type: 'string',
            min: 8,
            max: 8,
            message: '',
            trigger: 'blur',
          }
        ],
        sname: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        ssex: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        sbirthday: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        classno: [{
          required: true,
          message: ''
        }]
      },
      editRules: {
        sno: [{
          required: true,
          message: '',
          trigger: 'blur'
        },
          {
            type: 'string',
            min: 8,
            max: 8,
            message: '',
            trigger: 'blur',
          }
        ],
        sname: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        ssex: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        sbirthday: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        classno: [{
          required: true,
          message: ''
        }]
      },
      queryInfo: '',
      addDialogVisible: false,
      editDialogVisible: false,
      studentList: [],
      totalLength: 0,
      currentPage: 1,
      pageSize: 5,
      addForm: {
        sno: '',
        sname: '',
        ssex: '',
        sbirthday: '2001-01-23',
        classno: '',
      },
      editForm: {
        sno: '',
        sname: '',
        ssex: '',
        sbirthday: '',
        classno: ''
      },
      sexOptions: ['', ''],
      classOptions: [],
      classnoFilters:[],
    }
  },
  mounted() {
    // this.getStudentListByPage()
    this.getStudentList()
    this.getClassnoList()
  },
  methods: {
    async queryStudentList() {
      const {data: res} = await this.$http.get('student/query/' + this.queryInfo)
      this.studentList = res.data
    },

    modifyStudent() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        // 
        await this.$http.post('student/modify', {
          sno: this.editForm.sno,
          sname: this.editForm.sname,
          ssex: this.editForm.ssex,
          sbirthday: this.editForm.sbirthday,
          classno: this.editForm.classno
        })
        this.$message.success('')
        // 
        this.editDialogVisible = false
        // 
        this.getStudentList()
      })
    },
    showEditDialog(sno) {
      this.editForm = this.studentList.filter(obj => obj.sno === sno)[0];
      this.editDialogVisible = true;
    },
    deleteStudentById(sno) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        await this.$http.get('student/delete/' + sno);
        this.$message({
          type: 'success',
          message: '!'
        });
        this.getStudentList()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    createStudent() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        // 
        await this.$http.post('student/create', {
          sno: this.addForm.sno,
          sname: this.addForm.sname,
          ssex: this.addForm.ssex,
          sbirthday: this.addForm.sbirthday,
          classno: this.addForm.classno
        })
        this.$message.success('')
        // 
        this.addDialogVisible = false
        // 
        this.getStudentList()
      })
    },
    sizeChange(val) {
      this.pageSize = val
      this.getStudentListByPage();
    },

    currentChange(val) {
      this.currentPage = val;
      this.getStudentListByPage();
    },

    async getStudentListByPage() {
      const {data: res} = await this.$http.post('student/getStudentListByPage', {
        currentPage: this.currentPage,
        pageSize: this.pageSize
      });
      this.studentList = res.data.list;
      this.totalLength = res.data.length;
    },

    async getStudentList() {
      const {data: res} = await this.$http.get('student/getStudentList');
      this.studentList = res.data.list;
      this.totalLength = res.data.length;
    },

    async getClassnoList() {
      const {data: res} = await this.$http.get('class/getClassnoList');
      this.classOptions = res.data;
      for(let i in res.data)
        this.classnoFilters.push({text: res.data[i], value: res.data[i]});
    },

    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },

    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },

    filterSexHandler(value, row, column) {
      const property = column['property'];
      return row[property] === value;
    },

    filterClassnoHandler(value, row, column) {
      const property = column['property'];
      return row[property] === value;
    },

    queryChange() {
      if(this.queryInfo === '') {
        this.getStudentList()
      }
    }
  }
}
</script>

<style scoped>
  .item {
    width: 300px;
  }
</style>
