<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card style="margin-top: 10px">
      <el-row :gutter="20" style="margin-bottom: 10px">
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!---->
      <el-table :data="classList" border strip :default-sort = "{prop: 'classno', order: 'ascending'}">
        <el-table-column label="" prop="classno" sortable></el-table-column>
        <el-table-column label="" prop="classname" sortable></el-table-column>
        <el-table-column label="" prop="classmajor"></el-table-column>
        <el-table-column label="" prop="classdept" sortable
                         :filters="[{text: '', value: ''}, {text: '', value: ''}]"
                         :filter-method="filterDeptHandler"></el-table-column>
        <el-table-column label="" prop="studentnumber" sortable></el-table-column>
<!--        <el-table-column label="" width="120px">-->
<!--          <template slot-scope="scope">-->
<!--&lt;!&ndash;            <el-tooltip effect="dark" content="" placement="top" :enterable="false">&ndash;&gt;-->
