<!--              <el-button type="danger" icon="el-icon-delete" size="mini"-->
<!--                         @click="deleteClassById(scope.row.classno)"></el-button>-->
<!--            </el-tooltip>-->
<!--          </template>-->
<!--        </el-table-column>-->
      </el-table>

<!--      <el-pagination-->
<!--          @size-change="sizeChange"-->
<!--          @current-change="currentChange"-->
<!--          :current-page="currentPage"-->
<!--          :page-sizes="[5, 10, 20, 50]"-->
<!--          :page-size="pageSize"-->
<!--          layout="total, sizes, prev, pager, next, jumper"-->
<!--          :total="totalLength"-->
<!--      >-->
<!--      </el-pagination>-->

    </el-card>

    <!---->
    <el-dialog
        title=""
        :visible.sync="addDialogVisible"
        width="40%"
        @close="addDialogClosed">
      <!---->
      <el-form :model="addForm" :rules="addRules" ref="addFormRef" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="classno">
          <el-input v-model="addForm.classno" class="item"></el-input>
        </el-form-item>
        <el-form-item label="" prop="classname">
          <el-input v-model="addForm.classname" class="item"></el-input>
        </el-form-item>
        <el-form-item label="" prop="classmajor">
          <el-input v-model="addForm.classmajor" class="item" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="classdept">
          <el-select v-model="addForm.classdept" placeholder="" class="item">
            <el-option
                v-for="item in deptOptions"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <!---->
      <span slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible= false"> </el-button>
                <el-button type="primary" @click="createClass"> </el-button>
            </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Class",
  data() {
    // 
    const classnoRules = (rule, value, callback) => {
      // Axios
      this.$http.get('class/checkClassno/' + value)
          .then((res => {
            // 
            if (res.data.code === 0) {
              callback()
            } else {
              callback(new Error(""))
            }
          }))
          .catch((err) => {
            // 
            console.log(err)
          })
    }
    return {
      addRules: {
        classno: [{
          required: true,
          message: '',
          trigger: 'blur'
        },
          {
            validator: classnoRules,
            trigger: 'blur'
          },
          {
            type: 'string',
            min: 6,
            max: 6,
            message: '',
            trigger: 'blur',
          }
        ],
        classname: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        classmajor: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        classdept: [{
          required: true,
          message: '',
          trigger: 'blur'
        }]
      },
      addDialogVisible: false,
      classList: [],
      totalLength: 0,
      currentPage: 1,
      pageSize: 5,
      addForm: {
        classno: '',
        classname: '',
        classmajor: '',
        classdept: '',
      },
      deptOptions: ['', ''],
    }
  },
  mounted() {
    this.getClassList()
  },
  methods: {
    sizeChange(val) {
      this.pageSize = val
      this.getClassListByPage();
    },

    currentChange(val) {
      this.currentPage = val;
      this.getClassListByPage();
    },

    async getClassListByPage() {
      const {data: res} = await this.$http.post('class/getClassListByPage', {
        currentPage: this.currentPage,
        pageSize: this.pageSize
      });
      this.classList = res.data.list;
      this.totalLength = res.data.length;
    },

    async getClassList() {
      const {data: res} = await this.$http.get('class/getClassList');
      this.classList = res.data.list;
      this.totalLength = res.data.length;
    },

    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },

    createClass() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        // 
        await this.$http.post('class/create', {
          classno: this.addForm.classno,
          classname: this.addForm.classname,
          classmajor: this.addForm.classmajor,
          classdept: this.addForm.classdept,
        })
        this.$message.success('')
        // 
        this.addDialogVisible = false
        // 
        this.getClassList()
      })
    },

    filterDeptHandler(value, row, column) {
      const property = column['property'];
      return row[property] === value;
    },

    showEditDialog(classno) {
      this.editForm = this.classList.filter(obj => obj.classno === classno)[0];
      this.editDialogVisible = true;
    },

    deleteClassById(classno) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        await this.$http.get('class/delete/' + classno);
        this.$message({
          type: 'success',
          message: '!'
        });
        this.getClassList()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
  }
}
</script>

<style scoped>
.item {
  width: 300px;
}
