<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card style="margin-top: 10px">
      <el-row :gutter="20" style="margin-bottom: 10px">
        <el-col :span="6">
          <el-input placeholder="" v-model="queryInfo" clearable @input="queryChange">
            <el-button slot="append" icon="el-icon-search" @click="queryTeacherList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!---->
      <el-table :data="teacherList" border stripe :default-sort = "{prop: 'tno', order: 'ascending'}">
        <el-table-column label="" prop="tno" sortable></el-table-column>
        <el-table-column label="" prop="tname"></el-table-column>
        <el-table-column label="" prop="tsex"
                         :filters="[{text: '', value: ''}, {text: '', value: ''}]"
                         :filter-method="filterHandler"></el-table-column>
        <el-table-column label="" prop="tbirthday" sortable></el-table-column>
        <el-table-column label="" prop="ttitle"
                         :filters="[{text: '', value: ''}, {text: '', value: ''}, {text: '', value: ''}, {text: '', value: ''}]"
                         :filter-method="filterHandler"></el-table-column>
        <el-table-column label="" width="120px">
          <template slot-scope="scope">
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" size="mini"
                         @click="showEditDialog(scope.row.tno)"></el-button>
            </el-tooltip>
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" size="mini"
                         @click="deleteTeacherById(scope.row.tno)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

<!--      <el-pagination-->
<!--          @size-change="sizeChange"-->
<!--          @current-change="currentChange"-->
<!--          :current-page="currentPage"-->
<!--          :page-sizes="[5, 10, 20, 50]"-->
<!--          :page-size="pageSize"-->
<!--          layout="total, sizes, prev, pager, next, jumper"-->
<!--          :total="totalLength"-->
<!--      >-->
<!--      </el-pagination>-->

    </el-card>

    <!---->
    <el-dialog
        title=""
        :visible.sync="addDialogVisible"
        width="40%"
        @close="addDialogClosed">
      <!---->
      <el-form :model="addForm" :rules="addRules" ref="addFormRef" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="tno">
          <el-input v-model="addForm.tno" class="item"></el-input>
        </el-form-item>
        <el-form-item label="" prop="tname">
          <el-input v-model="addForm.tname" class="item"></el-input>
        </el-form-item>
        <el-form-item label="" prop="tsex">
          <el-select v-model="addForm.tsex" placeholder="" class="item">
            <el-option
                v-for="item in sexOptions"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="tbirthday">
          <el-date-picker
              v-model="addForm.tbirthday"
              type="date"
              :clearable="false"
              :editable="false"
              value-format="yyyy-MM-dd"
              placeholder=""
              class="item">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="ttitle">
          <el-select v-model="addForm.ttitle" placeholder="" class="item">
            <el-option
                v-for="item in titleOptions"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <!---->
      <span slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible= false"> </el-button>
                <el-button type="primary" @click="createTeacher"> </el-button>
            </span>
    </el-dialog>

    <!---->
    <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="40%"
        @close="editDialogClosed">
      <!---->
      <el-form :model="editForm" :rules="editRules" ref="editFormRef" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="tno" disabled>
          <el-input v-model="editForm.tno" class="item" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="tname">
          <el-input v-model="editForm.tname" class="item"></el-input>
        </el-form-item>
        <el-form-item label="" prop="tsex">
          <el-select v-model="editForm.tsex" placeholder="" class="item">
            <el-option
                v-for="item in sexOptions"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="tbirthday">
          <el-date-picker
              v-model="editForm.tbirthday"
              type="date"
              :clearable="false"
              :editable="false"
              value-format="yyyy-MM-dd"
              placeholder=""
              class="item">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="ttitle">
          <el-select v-model="editForm.ttitle" placeholder="" class="item">
            <el-option
                v-for="item in titleOptions"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <!---->
      <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisible= false"> </el-button>
                <el-button type="primary" @click="modifyTeacher()"> </el-button>
            </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Teacher",
  data() {
    // 
    const rulesSno = (rule, value, callback) => {
      // Axios
      this.$http.get('teacher/checkTno/' + value)
          .then((res => {
            // 
            if (res.data.code === 0) {
              callback()
            } else {
              callback(new Error(""))
            }
          }))
          .catch((err) => {
            // 
            console.log(err)
          })
    }
    return {
      addRules: {
        tno: [{
          required: true,
          message: '',
          trigger: 'blur'
        },
          {
            validator: rulesSno,
            trigger: 'blur'
          },
          {
            type: 'string',
            min: 6,
            max: 6,
            message: '',
            trigger: 'blur',
          }
        ],
        tname: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        tsex: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        tbirthday: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        ttitle: [{
          required: true,
          message: ''
        }]
      },
      editRules: {
        tno: [{
          required: true,
          message: '',
          trigger: 'blur'
        },
          {
            type: 'string',
            min: 6,
            max: 6,
            message: '',
            trigger: 'blur',
          }
        ],
        tname: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        tsex: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        tbirthday: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        ttitle: [{
          required: true,
          message: ''
        }]
      },
      queryInfo: '',
      addDialogVisible: false,
      editDialogVisible: false,
      teacherList: [],
      totalLength: 0,
      currentPage: 1,
      pageSize: 5,
      addForm: {
        tno: '',
        tname: '',
        tsex: '',
        tbirthday: '1970-01-01',
        ttitle: '',
      },
      editForm: {
        tno: '',
        tname: '',
        tsex: '',
        tbirthday: '',
        ttitle: ''
      },
      sexOptions: ['', ''],
      titleOptions: ['', '', '', ''],
    }
  },
  mounted() {
    this.getTeacherList()
  },
  methods: {
    modifyTeacher() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        // 
        await this.$http.post('teacher/modify', {
          tno: this.editForm.tno,
          tname: this.editForm.tname,
          tsex: this.editForm.tsex,
          tbirthday: this.editForm.tbirthday,
          ttitle: this.editForm.ttitle
        })
        this.$message.success('')
        // 
        this.editDialogVisible = false
        // 
        this.getTeacherList()
      })
    },

    showEditDialog(tno) {
      this.editForm = this.teacherList.filter(obj => obj.tno === tno)[0];
      this.editDialogVisible = true;
    },

    deleteTeacherById(tno) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        await this.$http.get('teacher/delete/' + tno);
        this.$message({
          type: 'success',
          message: '!'
        });
        this.getTeacherList()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    sizeChange(val) {
      this.pageSize = val
      this.getTeacherListByPage();
    },

    currentChange(val) {
      this.currentPage = val;
      this.getTeacherListByPage();
    },

    createTeacher() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        // 
        await this.$http.post('teacher/create', {
          tno: this.addForm.tno,
          tname: this.addForm.tname,
          tsex: this.addForm.tsex,
          tbirthday: this.addForm.tbirthday,
          ttitle: this.addForm.ttitle
        })
        this.$message.success('')
        // 
        this.addDialogVisible = false
        // 
        this.getTeacherList()
      })
    },

    async getTeacherListByPage() {
      const {data: res} = await this.$http.post('teacher/getTeacherListByPage', {
        currentPage: this.currentPage,
        pageSize: this.pageSize
      });
      this.teacherList = res.data.list;
      this.totalLength = res.data.length;
    },

    async getTeacherList() {
      const {data: res} = await this.$http.get('teacher/getTeacherList');
      this.teacherList = res.data.list;
      this.totalLength = res.data.length;
    },

    filterHandler(value, row, column) {
      const property = column['property'];
      return row[property] === value;
    },

    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },

    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },

    queryChange() {
      if(this.queryInfo === '') {
        this.getTeacherList()
      }
    },

    async queryTeacherList() {
      const {data: res} = await this.$http.get('teacher/query/' + this.queryInfo)
      this.teacherList = res.data
    }
  }
}
</script>

<style scoped>
.item {
  width: 300px;
}
</style>
