<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card style="margin-top: 10px">
      <el-row :gutter="20" style="margin-bottom: 10px">
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!---->
      <el-table :data="courseList" border stripe :default-sort = "{prop: 'cno', order: 'ascending'}">
        <el-table-column label="" prop="cno" sortable></el-table-column>
        <el-table-column label="" prop="cname"></el-table-column>
        <el-table-column label="" prop="ccredit" sortable></el-table-column>
        <el-table-column label="" width="120px">
          <template slot-scope="scope">
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" size="mini"
                         @click="showEditDialog(scope.row.cno)"></el-button>
            </el-tooltip>
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" size="mini"
                         @click="deleteCourseById(scope.row.cno)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

<!--      <el-pagination-->
<!--          @size-change="sizeChange"-->
<!--          @current-change="currentChange"-->
<!--          :current-page="currentPage"-->
<!--          :page-sizes="[5, 10, 20, 50]"-->
<!--          :page-size="pageSize"-->
<!--          layout="total, sizes, prev, pager, next, jumper"-->
<!--          :total="totalLength"-->
<!--      >-->
<!--      </el-pagination>-->

    </el-card>

    <!---->
    <el-dialog
        title=""
        :visible.sync="addDialogVisible"
        width="40%"
        @close="addDialogClosed">
      <!---->
      <el-form :model="addForm" :rules="addRules" ref="addFormRef" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="cno">
          <el-input v-model="addForm.cno" class="item"></el-input>
        </el-form-item>
        <el-form-item label="" prop="cname">
          <el-input v-model="addForm.cname" class="item"></el-input>
        </el-form-item>
        <el-form-item label="" prop="ccredit">
          <el-input v-model="addForm.ccredit" class="item"></el-input>
        </el-form-item>
      </el-form>
      <!---->
      <span slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible= false"> </el-button>
                <el-button type="primary" @click="createCourse"> </el-button>
            </span>
    </el-dialog>

    <!---->
    <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="40%"
        @close="editDialogClosed">
      <!---->
      <el-form :model="editForm" :rules="editRules" ref="editFormRef" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="cno" disabled>
          <el-input v-model="editForm.cno" class="item" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="cname">
          <el-input v-model="editForm.cname" class="item"></el-input>
        </el-form-item>
        <el-form-item label="" prop="ccredit">
          <el-input v-model="editForm.ccredit" class="item"></el-input>
        </el-form-item>
      </el-form>
      <!---->
      <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisible= false"> </el-button>
                <el-button type="primary" @click="modifyCourse()"> </el-button>
            </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Course",
  data() {
    // 
    const cnoRules = (rule, value, callback) => {
      // Axios
      this.$http.get('course/checkCno/' + value)
          .then((res => {
            // 
            if (res.data.code === 0) {
              callback()
            } else {
              callback(new Error(""))
            }
          }))
          .catch((err) => {
            // 
            console.log(err)
          })
    }

    const validateInteger = (rule, value, callback) => {
      if (value && !(/^[1-9]\d*$/).test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }

    return {
      addRules: {
        cno: [{
          required: true,
          message: '',
          trigger: 'blur'
        },
          {
            validator: cnoRules,
            trigger: 'blur'
          },
          {
            type: 'string',
            min: 6,
            max: 6,
            message: '',
            trigger: 'blur',
          }
        ],
        cname: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        ccredit: [{
          required: true,
          message: '',
          trigger: 'blur',
        },
          {
            validator: validateInteger,
            trigger: 'blur'
          }],
      },
      editRules: {
        cno: [{
          required: true,
          message: '',
          trigger: 'blur'
        },
          {
            type: 'string',
            min: 6,
            max: 6,
            message: '',
            trigger: 'blur',
          }
        ],
        cname: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        ccredit: [{
          required: true,
          message: '',
          trigger: 'blur',
        },
          {
            validator: validateInteger,
            trigger: 'blur'
          }],
      },
      addForm: {
        cno: '',
        cname: '',
        ccredit: 2,
      },
      editForm: {
        cno: '',
        cname: '',
        ccredit: ''
      },
      addDialogVisible: false,
      editDialogVisible: false,
      courseList: [],
      totalLength: 0,
      currentPage: 1,
      pageSize: 5,
    }
  },
  mounted() {
    // this.getCourseListByPage()
    this.getCourseList()
  },

  methods: {
    modifyCourse() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        // 
        await this.$http.post('course/modify', {
          cno: this.editForm.cno,
          cname: this.editForm.cname,
          ccredit: this.editForm.ccredit
        })
        this.$message.success('')
        // 
        this.editDialogVisible = false
        // 
        this.getCourseList()
      })
    },

    showEditDialog(cno) {
      this.editForm = this.courseList.filter(obj => obj.cno === cno)[0];
      this.editDialogVisible = true;
    },

    deleteCourseById(cno) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        await this.$http.get('course/delete/' + cno);
        this.$message({
          type: 'success',
          message: '!'
        });
        this.getCourseList()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },

    createCourse() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        // 
        await this.$http.post('course/create', {
          cno: this.addForm.cno,
          cname: this.addForm.cname,
          ccredit: this.addForm.ccredit,
        })
        this.$message.success('')
        // 
        this.addDialogVisible = false
        // 
        this.getCourseList()
      })
    },

    sizeChange(val) {
      this.pageSize = val
      this.getCourseListByPage();
    },

    currentChange(val) {
      this.currentPage = val;
      this.getCourseListByPage();
    },

    async getCourseListByPage() {
      const {data: res} = await this.$http.post('course/getCourseListByPage', {
        currentPage: this.currentPage,
        pageSize: this.pageSize
      });
      this.courseList = res.data.list;
      this.totalLength = res.data.length;
    },

    async getCourseList() {
      const {data: res} = await this.$http.get('course/getCourseList');
      this.courseList = res.data.list;
      this.totalLength = res.data.length;
    },

    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },

    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
  }
}
</script>

<style scoped>
.item {
  width: 300px;
}
</style>
